import {FaTimes} from "react-icons/all";

const Task = ({task, onDelete, onToggle}) => {

    return (
        <div onDoubleClick={() => onToggle(task.id)} className={`${task.reminder === true ? `border-l-4 border-teal-300` :``} bg-gray-100 m-1 p-2 pl-4`}>
            <div className={"flex"}>
                <h3 className={"text-xl"}>{task.text}</h3>
                <a className={'ml-auto mr-3 mt-2'} href="#"><FaTimes onClick={() => onDelete(task.id)} style={{color: 'teal'}}/></a>
            </div>
            <p className={"text-base"}>{task.day}</p>
        </div>
    )
}
export default Task