import React, { useState} from "react";
import Header from './components/Header';
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask"
function App() {

    const [showAddTask,setShowAddTask] = useState(false)

    const [tasks,setTasks] = useState(JSON.parse(localStorage.getItem('localStorage')) || [
        {
            id: 1,
            text: 'Dentist',
            day: 'Feb 5th at 2:30 pm',
            reminder: false,
        },
        {
            id: 2,
            text: 'Pog Champ',
            day: 'May 23th at 3:30 pm',
            reminder: true,
        }
    ])

    React.useEffect(() => {
        localStorage.setItem('localStorage',JSON.stringify(tasks));
    }, [tasks]);

    // Add task
    const addTask = (task) => {
        console.log(task)
        const id = Math.floor(Math.random() * 10000)+1
        const newTask = {id,...task}
        setTasks([...tasks,newTask])

        console.log(tasks)
    }

    // Delete task
    const deleteTask = (id) => {
        console.log('delete ', id)
        setTasks(tasks.filter((task) => id!== task.id ))

        console.log(tasks)
    }

    // Toggle reminder
    const onToggle = (id) => {
        setTasks(
            tasks.map((task) =>
            task.id === id ? {...task, reminder: !task.reminder}: task)
        )
    }


  return (
    <div className="border-2  m-20 p-10">
        <Header showAdd={showAddTask} onAdd={() => setShowAddTask(!showAddTask)} text={"Task Tracker"}/>
        {showAddTask===false
            ? ""
            : <AddTask onAdd={addTask}/>}
        {tasks.length>0
            ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={onToggle}/>
            : <h1 className={"mt-5 text-xl"}>No tasks to show!</h1>
        }
    </div>
  )
}

export default App;
