import PropTypes from "prop-types";

const Button = ({color, hoverColor, text, onClick}) => {



    return (
        <button onClick={onClick} className={color + " " + hoverColor + " ml-auto px-8 py-3 rounded"}>{text}</button>
    )
}

Button.defaultProps = {
    color: 'bg-teal-400',
    hoverColor : 'hover:bg-teal-500',
}
Button.propTypes = {
    color: PropTypes.string,
    hoverColor: PropTypes.string,
    onClick: PropTypes.func,
}
export default Button