import {useState} from "react"

const AddTask = ({onAdd}) => {
    const [text,setText] = useState('')
    const [day,setDay] = useState('')
    const [reminder,setReminder] = useState(false)

    const onSubmit = (e) => {
    e.preventDefault();

    if(!text){
        alert("Please add a task")
        return
    }

    onAdd({text,day,reminder})
        setText('')
        setDay('')
        setReminder(false)
    }

    return (
        <form action="" onSubmit={onSubmit}>
            <div className={"flex flex-col"}>
                <label className={"text-2xl"} htmlFor="">Task</label>
                <input value={text} onChange={(e) => setText(e.target.value)} className={"bg-gray-200 m-1 p-2 rounded border-2 border-black"} placeholder={"Add Task"} type="text"/>
            </div>
            <div className={"flex flex-col"}>
                <label className={"text-2xl"} htmlFor="">Day and Time</label>
                <input value={day} onChange={(e) => setDay(e.target.value)} className={"bg-gray-200 m-1 p-2 rounded border-2 border-black"} placeholder={"Add day and time"} type="text"/>
            </div>
            <div className={"flex flex-wrap"}>
                <label className={"text-2xl"} htmlFor="">Set Reminder</label>
                <input checked={reminder} value={reminder} onChange={(e) => setReminder(e.currentTarget.checked)} className={"ml-5 mt-3 form-checkbox"} type="checkbox"/>
            </div>

            <input className={"bg-teal-400 m-3 p-1 h-10 w-full rounded hover:bg-teal-500"} value={"Save task"} type="submit"/>
        </form>
    )
}
export default AddTask